.filters {
  height: 43px;
  background-color: #DFD297;
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.itemsLeft {
  font-weight: 700;
}

.activeView button {
  font: inherit;
  font-size: 13px;
  outline: 0;
  border: 2px solid transparent;
  border-radius: 15px;
  background-color: transparent;
  -webkit-transition: all .2s ease-out;
  -o-transition: all .2s ease-out;
  transition: all .2s ease-out;
  cursor: pointer;
}

.activeView button:hover {
  border: 2px solid #000;
}

.activeView button:not(:last-child) {
  margin-right: 5px;
}

.clearCompleted {
  font: inherit;
  font-size: 13px;
  display: inline;
  visibility: hidden;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  -webkit-transition: all .2s ease-out;
  -o-transition: all .2s ease-out;
  transition: all .2s ease-out;
}

.clearCompleted:hover {
  color: #C33149;
  border-bottom: 1px solid #C33149;
}

@media (max-width: 499px) {
  .itemsLeft {
    width: 50px;
    font-size: 12px;
  }

  .clearCompleted {
    width: 70px;
  }
}


